import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import { useCSVData } from './hooks/useCSVData';
import { useDataFilter } from './hooks/useDataFilter';
import DataTable from './components/DataTable';
import IngredientAnalysis from './components/IngredientAnalysis';
import SearchBar from './components/SearchBar';
import ProductList from './components/ProductList';
import './App.css';

Modal.setAppElement('#root');

function App() {
  const { data, columns } = useCSVData('/db.csv');
  const [visibleColumns, setVisibleColumns] = useState(['', '', '', '', '']);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [showAnalysis, setShowAnalysis] = useState(false);
  const [showProductList, setShowProductList] = useState(false);
  const [selectedIngredient, setSelectedIngredient] = useState('');
  const [selectedIngredients, setSelectedIngredients] = useState(null);
  const [browserWidth, setBrowserWidth] = useState(window.innerWidth);

  const { filteredData, searchTerms, setSearchTerms, excludeTerms, setExcludeTerms } = useDataFilter(data, columns);

  useEffect(() => {
    const handleResize = () => setBrowserWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const toggleModal = () => setModalIsOpen(!modalIsOpen);
  const toggleAnalysis = () => setShowAnalysis(!showAnalysis);
  const resetSearch = () => {
    setSearchTerms([{ value: '', type: 'AND' }]);
    setExcludeTerms([{ value: '' }]);
  };

  const openProductList = (ingredient, ingredients) => {
    setSelectedIngredient(ingredient);
    setSelectedIngredients(ingredients);
    setShowProductList(true);
  };

  const adjustedColumns = columns.map(column => {
    if (visibleColumns.includes(column.name)) {
      let width;
      switch (column.name) {
        case '':
          width = browserWidth * 0.15;
          break;
        case '':
          width = browserWidth * 0.2;
          break;
        case '':
          width = browserWidth * 0.25;
          break;
        case '':
          width = browserWidth * 0.25;
          break;
        case '':
          width = browserWidth * 0.15;
          break;
        default:
          width = browserWidth * 0.1;
      }
      return {
        ...column,
        width: `${width}px`,
        wrap: true,
        style: {
          whiteSpace: 'normal',
          overflow: 'hidden',
          textOverflow: 'ellipsis'
        }
      };
    }
    return column;
  });

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <h1 className="text-4xl font-bold mb-5 text-center text-gray-900">  </h1>
      <SearchBar
        searchTerms={searchTerms}
        setSearchTerms={setSearchTerms}
        excludeTerms={excludeTerms}
        setExcludeTerms={setExcludeTerms}
        onOpenModal={toggleModal}
        onShowAnalysis={toggleAnalysis}
        onResetSearch={resetSearch}
      />
      <DataTable
        columns={adjustedColumns.filter(col => visibleColumns.includes(col.name))}
        data={filteredData.length > 0 ? filteredData : data}
      />
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={toggleModal}
        contentLabel="Additional Information Modal"
      >
        <h2 className="text-lg font-medium mb-4">  </h2>
        <div className="space-y-2">
          {columns.map(column => (
            <div key={column.name} className="flex items-center">
              <input
                type="checkbox"
                id={column.name}
                checked={visibleColumns.includes(column.name)}
                onChange={() => {
                  if (visibleColumns.includes(column.name)) {
                    setVisibleColumns(visibleColumns.filter(col => col !== column.name));
                  } else {
                    setVisibleColumns([...visibleColumns, column.name]);
                  }
                }}
                className="mr-2"
              />
              <label htmlFor={column.name}>{column.name}</label>
            </div>
          ))}
        </div>
        <button onClick={toggleModal} className="mt-4 bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"></button>
      </Modal>
      <Modal
        isOpen={showAnalysis}
        onRequestClose={toggleAnalysis}
        contentLabel="Ingredient Analysis Modal"
        style={{
          content: {
            width: '80%',
            height: '80%',
            margin: 'auto'
          }
        }}
      >
        <IngredientAnalysis data={data} onClose={toggleAnalysis} onShowProducts={openProductList} />
      </Modal>
      <Modal
        isOpen={showProductList}
        onRequestClose={() => setShowProductList(false)}
        contentLabel="Product List Modal"
        style={{
          content: {
            width: '80%',
            height: '80%',
            margin: 'auto'
          }
        }}
      >
        <ProductList
          data={data}
          ingredient={selectedIngredient}
          ingredients={selectedIngredients}
          onClose={() => setShowProductList(false)}
        />
      </Modal>
    </div>
  );
}

export default App;